.experience-body-centered {
    width: 70%;
    text-align: center;
    margin: auto;
}

.experience-row {
    margin-top: 40px;
}

.experience-main-img {
    height: 375px;
    margin: auto;
}

@media screen and (max-width: 425px) {
    .experience-main-img {
        height: 200px;
        margin: auto;
    }
}

.box-experience {
    margin: auto;
}

.experience-h1 {
    padding: 15px;
}

.experience-outer {
    margin-top: 30px;
    border: 1px solid #516096;
    border-radius: 10px;
    padding: 20px;
}

.date-experience {
    text-align: right;
    font-style: italic;
}

.experience-name, .experience-subtext {
    text-align: left;
}

.experience-subtext {
    font-size: 1.3em;
}