.projects-main-img {
    height: 375px;
    margin: auto;
}

.projects-top-row {
    margin-bottom: 50px;
}

.box-projects {
    margin: auto;
}

.projects-body-centered {
    text-align: center;
    width: 70%;
}

.project-box-outer {
    border: 1px solid #516096;
    text-align: center;
    padding: 10px;
    margin: 5px;
    border-radius: 3px;
}

.project-name {
    color: #516096;
    font-size: 1.5em;
}

.project-description {
    text-align: left;
}

.p-projects-language {
    float: right;
    padding: 0 10px 0 5px;
}

.fa-projects-circle {
    float: right;
    margin-top: 5px;
}